function instance($$self, $$props, $$invalidate) {
	let currentBalance = 0;
	let loanPendingMonths = 0;
	let creditCardLimit = 0;
	let creditCardUsed = 0;
	let creditCardOverduePayment = 0;
	let hasLoan = 0;
	let hasCreditCard = 0;
	let error = "";
	let classification = "";
	let confidence = 0;

	function makePrediction() {
		const input = {
			currentBalance,
			loanPendingMonths,
			creditCardLimit,
			creditCardUsed,
			creditCardOverduePayment,
			hasLoan,
			hasCreditCard
		};

		console.log(input);

		predict(input, (_error, results) => {
			if (_error) {
				$$invalidate(7, error = _error.message);
			} else {
				const firstResult = results[0];
				$$invalidate(8, classification = firstResult.label);
				$$invalidate(9, confidence = firstResult.confidence * 100);
			}
		});
	}

	function reset() {
		$$invalidate(0, currentBalance = 0);
		$$invalidate(1, loanPendingMonths = 0);
		$$invalidate(2, creditCardLimit = 0);
		$$invalidate(3, creditCardUsed = 0);
		$$invalidate(4, creditCardOverduePayment = 0);
		$$invalidate(5, hasLoan = 0);
		$$invalidate(6, hasCreditCard = 0);
		$$invalidate(7, error = "");
		$$invalidate(8, classification = "");
		$$invalidate(9, confidence = 0);
	}

	function input_input_handler() {
		currentBalance = to_number(this.value);
		$$invalidate(0, currentBalance);
	}

	function select0_change_handler() {
		hasLoan = select_value(this);
		$$invalidate(5, hasLoan);
	}

	function input_input_handler_1() {
		loanPendingMonths = to_number(this.value);
		(($$invalidate(1, loanPendingMonths), $$invalidate(5, hasLoan)), $$invalidate(6, hasCreditCard));
	}

	function select1_change_handler() {
		hasCreditCard = select_value(this);
		$$invalidate(6, hasCreditCard);
	}

